.vh {
  @include vh; }

.section-price__table {

  @include to($tablet) {
    margin: 0 -20px; } }

.price {
  display: flex;

  &__item {
    margin: 0; }

  &__item:first-child {
    width: 50%; }

  &__item:not(:first-child) {
    width: 25%; }

  &__item:not(:first-child) .price__title {
    @include vh; }

  &__item:first-child .price__head {
    width: 50%;
    margin-left: auto;
    border-left: 2px solid rgba(#fff, 0);

    @include to($tablet) {
      width: 100%; } }

  &__head {
    position: relative;
    top: 2px;
    z-index: 2;

    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 98px;
    border-right: 2px solid #fff;
    margin: 0;

    font-weight: 700;
    line-height: 1;
    text-transform: uppercase;
    text-align: center;

    background-color: #283645;
    color: #fff;

    @include to($desktop) {
      height: 80px;
      font-size: 16px; }

    @include to($tablet) {
      height: 100px; }

    .usd {
      display: inline-block;
      width: 100%;
      margin-top: 10px;

      font-size: 24px;
      font-weight: normal;

      color: rgba(#fff, .7);

      @include to($desktop) {
        font-size: 18px; }

      @include to($tablet) {
        font-size: 24px; } } }

  &__head-hit {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: -1px;
      left: -1px;

      display: block;
      width: 61px;
      height: 61px;

      background: url(../img/bg-hit-tablet.svg) no-repeat; }

    &:after {
      content: 'хит';
      position: absolute;
      top: 14px;
      left: 2px;
      transform: rotate(-45deg);

      font-size: 14px;
      font-weight: 700;
      text-transform: uppercase; } }


  $color-grid: #e5e5e5;

  &__options {
    padding: 0;
    margin: 0;
    border-bottom: 1px solid $color-grid;
    border-right: 0;

    background: #fff; }

  &__item:last-child .price__options {
    border-right: 1px solid $color-grid;

    @include to($tablet) {
      border-right: 0; } }

  &__item:first-child .price__options {
    border-left: 1px solid $color-grid;

    @include to($tablet) {
      border-left: 0; } }

  &__option {
    display: flex;
    height: 60px;
    line-height: 60px;

    @include to($desktop) {
      height: 48px;
      line-height: 48px; }

    @include to($tablet) {
      height: 62px;
      line-height: 62px;
      text-transform: uppercase; } }


  &__title {
    width: calc(50% - 20px);
    padding-left: 20px;
    border: 1px solid $color-grid;

    @include to($desktop) {
      font-size: 14px; }

    @include to($tablet) {
      font-size: 14px;
      width: 210px;
      border-right: 0;
      border-left: 0; } }

  &__item:first-child .price__option:first-child .price__title {
    border-top: 2px solid $color-grid; }

  &__value {
    width: 50%;
    border: 1px solid $color-grid;

    @include to($tablet) {
      width: 70px; } }

  &__item:not(:first-child) .price__value {
    width: 100%; } }

.value-yes,
.value-no {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    @include vcenter;

    display: block;
    width: 31px;
    height: 21px;

    background: url(../img/icon-table-yes.svg); } }

.value-no {
  &:before {
    width: 26px;
    height: 26px;

    background: url(../img/icon-no.svg); } }


.p-slider__dots {
  position: relative; }
