.entries {
  @include df;
  @include row-flex;
  justify-content: space-between;
  overflow: hidden; }

.entry {
  @include col;
  @include sz(4);
  @include df;
  flex-direction: column;
  margin-bottom: 50px;

  @include to($desktop) {
    @include sz(6); }

  @include to($tablet) {
    @include sz(12); }

  &-full {
    width: 100%;
    margin-bottom: 20px;

    @include to($desktop) {
      width: 100%; }

    @include to($tablet) {
      padding-bottom: 30px; } }

  &__photo-full {
    position: relative;

    display: block;
    max-width: 1200px;
    margin: 0 auto;

    font-size: 0;
    text-align: center;

    @include to($desktop) {
      max-width: 660px; }

    @include to($tablet) {
      max-width: 320px; } }

  &__button {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 140px;
    height: 36px;
    border: 0;

    font-size: 12px;
    font-weight: 700;
    line-height: 36px;
    text-transform: uppercase;

    background-color: $red;
    color: white;

    @include tr(filter, .3s);

    @include to($desktop) {
      top: 55px; }

    @include to($tablet) {
      top: 0;
      right: 0; }

    &:hover {
      filter: brightness(1.1); } }

  &__content {
    @include df;
    flex-grow: 1;
    align-items: baseline;
    box-sizing: border-box;
    padding: 10px 25px;

    background: $beige; }

  &__content-column {
    box-sizing: border-box;
    flex-direction: column;
    min-height: 160px;
    padding-bottom: 30px;

    @include to($tablet) {
      min-height: auto; } }

  &__photo {
    font-size: 0; }

  &__name {
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 700; }

  &__time,
  &__like {
    font-size: 12px;
    font-weight: 300;
    text-transform: uppercase; }

  &__like {
    margin-bottom: 0;
    margin-left: auto;
    font-weight: 400;
    color: $red;

    @include to($tablet) {
      order: 5;
      margin-right: auto;
      margin-left: 0; } }


  &__text {
    margin-top: 0;
    font-size: 14px; }

  &-last {
    order: 5;
    margin-top: auto;
    margin-left: 0; } }

.heart {
  width: 13px;
  height: 12px;
  border: 0;
  margin-left: 10px;

  background: url(../img/icon-heart.svg) no-repeat;
  outline: 0;
  cursor: pointer;

  @include tr(transform, .3s);

  &:hover {
    transform: scale(1.3); } }

