
.promo-declaration {
  @include df;
  padding: 105px 0 10px;

  @include to($desktop) {
    padding-top: 65px;
    justify-content: space-between; }

  @include to($tablet) {
    display: none; }


  &__image {
    @include w(370, $container-desktop);

    @include to($desktop) {
      width: 49%; } }

  &__content {
    @include w(570, $container-desktop);

    @include to($desktop) {
      width: 49%; } }

  &__title {
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: 700;

    span {
      @include to($desktop) {
        display: inline-block;
        width: 100%; } }

    @include to($desktop) {
      margin-bottom: 10px;
      font-size: 30px;
      line-height: 1.1; } }

  &__text {
    margin-bottom: 32px;

    @include to($desktop) {
      margin-bottom: 12px;
      font-size: 14px; } }

  &__more {
    color: $red;

    @include to($desktop) {
      font-size: 14px; } } }


.hrono-img {
  position: relative;
  top: -60px;

  width: 298px;
  height: 345px;

  @include to($desktop) {
    top: -20px;
    width: 240px;
    height: 284px; }

  &:before {
    content: '';
    @include vcenter;

    display: block;
    width: 100%;
    height: 100%;

    background: url(../img/icon-timer.svg) center center no-repeat;
    background-size: contain; }

  &:after {
    content: '24';
    position: absolute;
    left: 50%;
    bottom: 24px;
    transform: translateX(-50%);

    box-sizing: border-box;
    display: block;
    width: 250px;
    height: 250px;
    border: 16px solid $red;
    border-radius: 50%;

    font-size: 120px;
    font-weight: 700;
    text-align: center;
    color: $red;

    @include to($desktop) {
      width: 200px;
      height: 200px;
      font-size: 96px; } } }

