.form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  font-family: Arial, opensans, sans-serif;

  &__fieldset--os {
    @include w(435, 940);

    @include to($desktop) {
      width: 100%; }

    @include to($tablet) {
      order: 3; } }

  &__fieldset--username {
    border: 0;
    margin: 0;
    padding: 0;
    @include w(435, 940);

    @include to($desktop) {
      width: 100%; }

    @include to($tablet) {
      order: 0; } }

  &__fieldset--contacts,
  &__fieldset--achievement,
  &__fieldset--emotions {
    @include col;
    @include sz(12); }

  &__fieldset--achievement {
    @include to($tablet) {
      order: 1;
      background-color: $beige; } }

  &__fieldset--contacts {
    @include to($tablet) {
      order: 2; } }

  &__fieldset--emotions {
    @include to($tablet) {
      order: 4; } }

  &__submit {
    @include to($tablet) {
      order: 5; } }

  &__submit {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 40px; }


  &__button {
    display: block;
    padding: 16px 47px;
    border: 0;

    font-size: 18px;
    font-weight: 700;
    line-height: 30px;
    text-align: center;

    color: #fff;
    background-color: #2aab6d;
    text-transform: uppercase;
    border-radius: 3px;
    cursor: pointer;

    transition: color .3s, background-color .3s;

    @include to($tablet) {
      margin-bottom: 20px; }


    &:hover {
      background-color: #1c9b5e; }

    &:active {
      color: rgba(255,255,255,.3);
      background-color: #1c9b5e; } }

  &__note {
    font-size: 14px;

    color: #283645;
    text-transform: uppercase;

    span {
      color: $red; } } }



$iheight: 62px;

.input {
  flex-grow: 1;

  height: $iheight;
  box-sizing: border-box;
  border: 2px solid #f2f2f2;
  padding: 0 20px;
  margin-left: 23px;

  font-size: 18px;
  line-height: $iheight;

  color: #283645;
  border-radius: 2px;

  @include tr(border-color, .3s);

  &:hover {
    border-color: #000; }

  &--username {
    @include to($tablet) {
      width: 100%;
      margin-left: 0; } }

  &--full {
    width: 100%;
    margin-left: 0; }

  &--full + .label {
    margin-bottom: 10px; }

  &--phone,
  &--mail {
    display: block;
    position: relative;

    &:after {
      content: '';
      position: absolute;
      top: 2px;
      right: 2px;
      bottom: 2px;

      display: block;
      width: $iheight;

      background: #e5e5e5 url(../img/icon-phone.svg) no-repeat center;

      @include to($tablet) {
        display: none; } } }

  &--mail {
    &:after {
      background-image: url(../img/icon-mail.svg); } } }

.textarea {
  box-sizing: border-box;
  min-height: 120px;
  width: 100%;
  padding: 13px 18px;
  border: 2px solid #e5e5e5;

  font-size: 18px;
  color: #283645;
  resize: none;

  @include tr(border-color, .3s);

  @include to($desktop) {
    min-height: 160px; }

  &:hover {
    border-color: #000; } }

.fieldset {
  box-sizing: border-box;
  margin-bottom: 60px;

  @include to($tablet) {
    margin: 0;
    margin-bottom: 60px;
    padding: 0; }


  &:not(.form__fieldset--username) {
    background-color: #fff;
    border: 2px solid #f2f2f2;

    @include to($tablet) {
      border: 0; } }


  &__items {
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

    padding: 30px 40px;

    @include to($tablet) {
      height: auto;
      margin-top: 10px;
      padding: 0; }

    &--username {
      padding: 0;
      padding-top: 10px; }

    &--contacts {
      flex-direction: row;
      padding-bottom: 0; }

    &--contacts  .label {
      height: 40px;

      @include to($desktop) {
        height: $iheight; } }


    &--achievement {
      flex-direction: row;

      @include to($desktop) {
        flex-direction: column; } } }

  &__item--username {
    @include to($tablet) {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      height: auto;
      line-height: 1.5; } }

  &__item:last-child {
    margin-bottom: 0; }

  &__item--half {
    @include w(435, 940);

    @include to($desktop) {
      width: 100%; } }

  &__item--achievement {
    box-sizing: border-box;
    width: 33%;

    @include to($desktop) {
      width: 100%;
      margin-bottom: 15px; }

    &:nth-child(3n+2),
    &:nth-child(3n+3) {
      padding-left: 20px;

      @include to($desktop) {
        padding-left: 0; } } } }

.legend {
  padding: 0 25px;

  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-align: center;

  color: $red;
  background-color: #fff; }

.label {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: $iheight;

  font-size: 14px;
  line-height: $iheight;

  color: $red;
  text-transform: uppercase;

  &__descr {
    @include to($tablet) {
      width: 100%;
      padding-bottom: 10px;
      padding-left: 20px; } }

  &:not(:last-child) {
    margin-bottom: 20px; }

  &--radio,
  &--checkbox {
    position: relative;

    padding-left: 55px;

    font-size: 18px;

    color: #283645;
    text-transform: none;
    cursor: pointer;

    &:before {
      content: '';
      position: absolute;
      top: 13px;
      left: 0;

      display: inline-block;
      width: 15px;
      height: 15px;
      padding: 10px;
      background-color: #fff;
      border: 2px solid #e5e5e5;

      @include tr(border-color, .3s); }

    &:hover {
      &:before {
        border-color: #000; } } }


  &--radio {
    &:before {
      border-radius: 50%; } } }


.input--radio,
.input--checkbox {
  @include vh;

  &:checked + .label--radio:after {
    content: '';
    position: absolute;
    top: 25px;
    left: 12px;
    width: 15px;
    height: 15px;
    border-radius: 50%;

    background-color: $red; }

  &:checked + .label--checkbox:after {
    content: '';
    position: absolute;
    top: 25px;
    left: 10px;
    width: 22px;
    height: 17px;

    background: url(../img/icon-yes.svg) no-repeat center;
    background-size: 22px 17px; } }

