.section-map {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 120px;

    @include to($desktop) {
      height: 75px; }

    background-image: linear-gradient(to top right, transparent 50%, #fff 50%), linear-gradient(to top left, transparent 50%, #fff 50%);
    background-position: top left, top right;
    background-size: 50.1% 100%;
    background-repeat: no-repeat; } }

.map__iframe {
  box-sizing: border-box;
  width: 100%;
  height: 525px;
  display: block;

  @include to($desktop) {
    height: 380px; } }
