
.header {
  position: relative;
  z-index: 10;
  background: rgba(#000, .3);

  &__wrap {
    display: flex;
    height: 108px;
    align-items: center; }

  &__logo {
    position: relative;
    top: -5px; }

  &__nav {
    margin-left: auto; }

  .burger {
    display: none;
    width: 50px;
    outline: none;

    span,
    span:before,
    span:after {
      @include tr(background, .3s); } }

  .burger--close span {
    width: 30px; } }

.nav {
  display: flex;

  color: #fff;

  &__item {
    @include tr(color, .3s);

    &:hover {
      color: $red; }

    &:active {
      color: rgba(#fff, .3); } }


  &__item:not(:first-child) {
    margin-left: 80px; }

  @include from($desktop) {
    .nav__item--is-active {
      position: relative;

      &:before {
        content: '';
        display: block;
        width: 100%;
        height: 2px;
        position: absolute;
        bottom: 0;
        left: 0;
        background: #fff;
        @include tr(background, .3s); } }

    .nav__item--is-active:hover {
      &:before {
        background-color: $red; } } }

  a {
    text-decoration: none;
    color: inherit; } }


@include to($desktop) {
  .header {

    &.menu-open {
      background: #2E3846; }

    &__wrap {
      display: block;
      height: auto; }

    &__logo {
      height: 109px;
      display: flex;
      justify-content: space-between;
      align-items: center; }

    &__nav {
      overflow: hidden;
      height: 0;
      opacity: 0;
      width: 100%;
      text-align: center;
      @include tr(all, .5s); }

    .js-nav-is-open {
      height: auto;
      opacity: 1; }

    .burger {
      display: block; } }

  .nav {
    display: block;
    padding-bottom: 2px;

    &__item {
      display: block;
      height: 63px;
      line-height: 63px;

      text-transform: uppercase;

      box-shadow: 0 1px 0 black, inset 0 1px 0 rgba(255, 255, 255, 0.15);

      // position: relative

      // &:before
      //   content: ''
      //   +vcenter
      //   height: 100%
      //   width: 100vw

 }      //   box-shadow: 0 1px 0 black, inset 0 1px 0 rgba(255, 255, 255, 0.15)

    &__item:not(:first-child) {
      margin-left: 0; } } }


@include to($tablet) {
  .header__logo {
    height: 70px; } }


.burger:hover {
  span,
  span:before,
  span:after {
    background-color: $red; } }

.burger--close:hover {
  > span {
    background: transparent; } }
