.hero {
  height: 745px;
  margin-top: -108px;
  padding-top: 108px;

  text-align: center;

  background: url(../img/bg-back-desktop@1x.jpg) center center no-repeat;
  background-size: cover;
  color: #fff;

  @include to($desktop) {
    height: 625px;
    background: url(../img/bg-back-tablet@1x.jpg) center center no-repeat;
    background-size: cover; }

  @include to($tablet) {
    position: relative;
    height: 400px;
    margin-bottom: 265px;

    background: url(../img/bg-back-mobile@1x.jpg) center center no-repeat;
    background-size: cover; }

  .container {
    height: 100%; }

  &__wrap {
    height: 100%;

    background-image: url(../img/iphone-hand-desktop@1x.png);
    background-position: -15% 100%;
    background-repeat: no-repeat;

    @include to($desktop) {
      background-image: url(../img/iphone-hand-tablet@1x.png);
      background-position: 50% 100%; }

    @include to($tablet) {
      margin: 0 -20px;
      padding: 0 20px;
      background-image: url(../img/iphone-hand-mobile@1x.png);
      background-position: 0 100%; } }

  &__content-mobile {
    height: 100%;

    @include to($tablet) {
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      height: 270px;
      background: #242A34; } }

  &__content {
    box-sizing: border-box;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    padding: 110px 0;

    @include to($desktop);

    @include to($tablet) {
      width: 320px;
      padding: 25px 0;
      margin: 0 auto; }


    a {
      text-decoration: none;
      color: inherit; } }


  &__title {
    font-weight: 300;
    font-size: 50px;
    line-height: 60px;

    @include to($desktop) {
      font-size: 40px;
      line-height: 50px; }

    @include to($tablet) {
      display: none; } }

  &__title-middle {
    @include vcenter;
    top: 44%;
    width: 100%; }

  &__icon {
    position: relative;
    top: 20px; }

  &__legend {
    font-weight: 300;
    line-height: 24px;

    @include to($tablet) {
      margin-bottom: 0;
      font-size: 14px; } }

  .icon {
    padding: 0 17px; }

  .icon-logo-apple {
    width: 46px;
    height: 56px; }

  .icon-logo-android {
    width: 42px;
    height: 47px; }

  .icon-logo-microsoft {
    width: 43px;
    height: 41px; } }


.hero__short {
  height: 287px;

  @include to($desktop) {
    height: 335px; }

  @include to($tablet) {
    height: 170px;
    margin-bottom: 0; }

  .hero__title {
    @include to($desktop) {
      font-size: 50px;
      line-height: 60px; }

    @include to($tablet) {
      display: block;

      font-size: 24px; } }

  .hero__title--compact {
    @include to($tablet) {
      line-height: 1.5; } } }
