
.moove {
  margin-bottom: 0;

  @include to($desktop) {
    margin-top: -15px; }

  &__list {
    @include df;
    @include row-flex;
    justify-content: center; }

  &__item {
    @include col;
    @include sz(4);

    @include to($desktop) {
      @include sz(6);
      margin-bottom: 20px; }

    @include to($tablet) {
      @include sz(12);
      margin-bottom: 5px; }

    text-align: center; }

  &__item:nth-child(2) {
    @include to($desktop) {
      order: 5; } }

  &__title {
    margin-bottom: 16px;

    font-weight: 700;
    color: #283645;

    @include to($desktop) {
      margin-bottom: 5px; }

    @include to($tablet) {
      margin-bottom: 0; } }

  &__value {
    margin: 0;

    font-size: 24px;
    line-height: 1.3;
    font-weight: 300;
    color: $red;

    a {
      text-decoration: none;
      color: inherit;

      @include tr(color, .3s);

      &:hover {
        text-decoration: underline;
        color: #283645; }

      &:active {
        color: rgba(#283645, .3); } } } }

