
.features {
  @include df;
  justify-content: space-between;

  @include to($desktop) {
    position: relative;
    flex-direction: column;
    padding-top: 88px; }

  @include to($desktop) {
    padding-top: 100px; } }

.feature {
  @include col;
  @include sz(4);

  text-align: center;

  @include to($desktop) {
    width: 49%;
    margin-left: auto;
    margin-bottom: 10px;
    text-align: left; }

  @include to($tablet) {
    @include sz(12);
    margin-bottom: 5px;
    text-align: center; }

  &__image {
    width: 292px;
    height: 302px;
    margin-bottom: 50px;
    overflow: hidden;

    background: url(../img/iphone-screen-1-desktop@1x.png) center bottom no-repeat;
    background-size: contain;

    @include to($desktop) {
      display: none; } }

  &__title {
    font-size: 30px;
    line-height: 1;
    font-weight: 700;

    @include to($desktop) {
      margin-bottom: 15px;
      font-size: 18px; } }

  &__text {
    padding: 0 20px;
    color: #282645;

    @include to($desktop) {
      font-size: 14px;
      padding: 0; } } }

.feature:nth-child(2) {

  .feature__image {
    background-image: url(../img/iphone-screen-2-desktop@1x.png); } }

.feature:nth-child(3) {

  .feature__image {
    background-image: url(../img/iphone-screen-3-desktop@1x.png); } }


.features:after {

  @include to($desktop) {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;

    display: block;
    width: 270px;
    height: 485px;
    background: url(../img/iphone-screen-3-tablet@1x.png) 0 bottom no-repeat;
    background-size: contain; }

  @include to($tablet) {
    position: static;
    width: 277px;
    height: 280px;
    margin-top: 20px;
    background-position: center bottom; } }
