$beige: #f2f2f2;
$red: #d22856;
$desktop: 960;
$tablet: 660;
$mobile: 320;
$container-desktop: 940;

// html
//   box-sizing: border-box
// *,
// *::before,
// *::after
//   box-sizing: inherit


.to-top {
  background-color: darken($beige, 10%); }

img {
  max-width: 100%;
  height: auto; }

body {
  @include os;
  font-size: 18px;
  line-height: 1.66;

  color: #000; }

.container {
  width: 940px;
  margin: 0 auto;
  padding: 0 10px;

  @include to($desktop) {
    width: 620px;
    padding: 0 20px; }

  @include to($tablet) {
    width: 280px;
    padding: 0 20px; } }


@include from($desktop) {
  .beige-desk {
    background-color: $beige; } }


.beige {
  background-color: $beige;
  overflow: hidden; }

.beige-diag {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 100px;
    width: 100%;

    background-image: linear-gradient(177deg, #fff 49%, $beige 50%, $beige 100%); } }

.diag-wrap {
  position: relative;
  z-index: 1; }

.relative-wrap {
  position: relative; }

.section {
  padding-top: 25px;

  &__title {
    margin-bottom: 32px;

    font-weight: 300;
    font-size: 50px;
    line-height: 60px;
    text-align: center;

    color: #283645;

    @include to($desktop) {
      font-size: 40px;
      line-height: 1.2; }

    @include to($tablet) {
      font-size: 24px;

      span {
        display: inline-block;
        margin-bottom: 30px; } } }

  &__title-img {
    margin-bottom: 55px;

    @include to($desktop) {
      display: none; } }

  &__title-before-diag {
    margin-bottom: 80px;

    @include to($desktop) {
      margin-top: 20px;
      margin-bottom: 20px; }

    @include to($tablet) {
      margin-bottom: 0; }


    .icon {
      width: 79px;
      height: 64px; } } }

.section-comments {
  padding-top: 70px;
  margin-bottom: 30px;

  @include to($tablet) {
    padding-top: 40px; } }

.section-price {
  padding-top: 160px;
  padding-bottom: 100px;

  @include to($desktop) {
    padding-top: 120px;
    padding-bottom: 60px; }

  @include to($tablet) {
    padding-top: 110px; } }

.section-promo {
  margin-bottom: 10px; }

.section-entries {
  margin-bottom: -85px; }

.section-configurator {
  margin-top: -25px;
  margin-bottom: 30px;

  .container {

    @include to($desktop) {
      width: 660px;
      padding: 0; }

    @include to($tablet) {
      width: 320px; } } }


.button {
  $h: 48px;
  display: inline-block;
  height: $h;
  border: 3px solid white;
  border-radius: 30px;
  padding: 0 25px;
  margin: 0 auto;

  font-weight: 700;
  font-size: 21px;
  line-height: $h;

  cursor: pointer;
  transition: all .3s;

  &:hover {
    color: $red;
    background-color: #fff; }

  &:active {
    color: rgba($red, .3); }


  @include to($tablet) {
    padding: 0 35px;
    font-size: 18px; } }

.button-instrument {
  height: 55px;
  border: 3px solid $red;
  padding-right: 40px;
  padding-left: 40px;
  margin: 0;

  font-size: 18px;

  background-color: transparent;
  color: $red;

  &:disabled {
    opacity: 0.1;
    border-color: #283645;
    color: #283645; }

  &:hover {
    background-color: $red;
    color: #fff;

    &:disabled {
      opacity: 0.3;
      background-color: transparent;
      color: #283645; } } }


.hide-mobile {
  @include to($tablet) {
    display: none; } }

.vh {
  @include vh; }

