.configurator {
  display: flex;
  padding: 60px 0;

  @include to($tablet) {
    display: block;
    padding: 20px 0 40px; }

  &__image {
    width: 50%;

    @include to($desktop) {
      width: #{calc(50% - 10px)};
      margin-right: auto; }

    @include to($tablet) {
      width: #{calc(100% - 20px)};
      margin: auto;
      margin-bottom: 20px; } }

  img {
    display: block;
    max-width: 300px;
    margin: auto;

    @include to($desktop) {
      margin-right: 0; }

    @include to($tablet) {
      max-width: 100%;
      margin: auto; } }

  &__redactor {
    width: 50%;

    @include to($desktop) {
      width: #{calc(50% - 10px)}; }

    @include to($tablet) {
      width: 100%; }

    @include to($tablet) {
      width: #{calc(100% - 20px)};
      margin: auto; } } }

.instrument {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  padding-right: 60px;

  @include to($desktop) {
    padding-right: 20px;
    padding-left: 10px; }

  &__item {
    display: flex;
    border-bottom: 1px solid rgba(#000, .1); }

  &__item:not(:first-child) &__icon {
    padding-top: 12px; }

  &__icon {
    width: 48px;
    height: 48px;
    padding-bottom: 10px; }

  .icon {
    font-size: 48px;
    fill: $red;

    @include to($desktop) {
      font-size: 28px;
      position: relative;
      top: 10px; } }

  .icon-logo-icon-editor-contrast {
    position: relative;
    top: 5px;
    left: 5px;
    font-size: 37px;

    @include to($desktop) {
      font-size: 22px; } }

  &__range {
    display: flex;
    align-items: center;
    flex-grow: 1;
    padding: 0 30px;

    @include to($desktop) {
      padding: 0 15px 0 0; } }

  &__buttons {
    display: flex;
    justify-content: space-between;
    margin-top: auto;

    @include to($desktop) {
      flex-direction: column; }

    @include to($tablet) {
      margin-top: 30px; }

    .button:last-child {
      @include to($desktop) {
        margin-top: 20px; } } }


  .rangeslider__fill,
  .rangeslider__fill__bg {
    background: #000; }

  .rangeslider__handle {
    border-color: $red;
    background-color: #fff;

    &:active {
      background-color: $red; } } }
