
.q-slider {
  position: relative; }

.q-slider__prev,
.q-slider__next {
  position: absolute;
  top: 17%;
  left: 3px;
  z-index: 10;

  display: block;
  border: solid rgba(#283645, .3);
  border-width: 0 2px 2px 0;
  padding: 14px;

  transform: rotate(135deg);
  cursor: pointer;
  transition: border-color, .3s;

  &:hover {
    border-color: rgba(#283645, .8); }

  @include to($desktop) {
    display: none; } }

.q-slider__next {
  left: auto;
  right: 3px;
  transform: rotate(-45deg); }


.slick-dots {
  z-index: 10;
  bottom: -50px;

  @include to($tablet) {
    bottom: -20px; }

  li {
    width: 15px;
    height: 15px; }

  li button:before {
    content: '';
    @include vcenter;
    display: inline-block;
    width: 6px;
    height: 6px;

    background-color: #283645;
    border-radius: 50%; }

  li.slick-active button:before {
    border: 3px solid #3f3f3f;
    background-color: #fff; } }



.quotes {
  text-align: center; }

.quote {
  box-sizing: border-box;
  padding: 0 80px;

  @include to($desktop) {
    padding: 0; }

  &__wrap {
    @include to($tablet) {
      display: flex;
      flex-direction: column-reverse; } }

  &__text {
    margin-bottom: 30px;

    @include to($tablet) {
      font-size: 14px;
      line-height: 30px; } }

  &__name {
    font-size: 24px;
    font-weight: 700;

    @include to($desktop) {
      margin-bottom: 10px;
      font-size: 20px; }

    @include to($tablet) {
      margin-bottom: 20px;
      font-size: 18px; } }

  &__status {
    color: #283645;

    @include to($desktop) {
      font-size: 14px; }

    @include to($tablet) {
      @include vh; } } }
