@include font(opensans, opensans);
@include font(opensans, opensansbold, 700);
@include font(opensans, opensanslight, 300);


@mixin os {
  font-family: opensans, sans-serif;
  font-weight: normal; }

@mixin osb {
  font-family: opensans, sans-serif;
  font-weight: 700; }

@mixin osl {
  font-family: opensans, sans-serif;
  font-weight: 300; }

