
.footer {

  &__wrap {
    @include df;
    justify-content: space-between;
    padding: 70px 0 50px;

    @include to($desktop) {
      padding: 30px 0; }

    @include to($tablet) {
      justify-content: center; } }

  &__logo .logo {

    @include to($tablet) {
      width: 146px;
      height: 43px;
      margin-bottom: 30px;

      background-image: url(../img/logotype-blue-tablet.svg); } }


  &__creaters {
    @include df;
    align-items: center;

    @include to($desktop) {
      font-size: 14px; }

    .icon {
      width: 26px;
      height: 34px;
      margin-left: 15px;
      fill: $red;

      cursor: pointer;
      @include tr(fill, .3s);

      &:hover {
        fill: #283645; }

      &:active {
        fill: rgba(#283645, .3); } } }


  &__sociate {

    @include to($tablet) {
      margin-bottom: 35px; } } }



.sociate__icon {
  position: relative;

  box-sizing: {}
  display: inline-block;
  width: 49px;
  height: 49px;
  margin: 0 5px;

  text-decoration: none;

  @include to($desktop) {
    top: 10px;
    left: -15px;
    width: 45px;
    height: 45px; }

  @include to($tablet) {
    left: 0; }


  &:hover {
    text-decoration: none;

    &:before {
      border-color: #283645; } }

  &:active {
    opacity: .5;

    &:before {
      border-color: rgba(#283645, .1); } }

  &:before {
    content: '';
    position: absolute;
    @include vcenter;

    width: 100%;
    height: 100%;
    border: 3px solid rgba(#283645, .1);
    border-radius: 50%;

    @include tr(all, .3s); }


  .icon {
    @include vcenter;
    fill: $red; }

  .logo-icon-twitter {
    width: 16px;
    height: 13px; }

  .logo-icon-facebook {
    width: 10px;
    height: 18px; }

  .logo-icon-youtube {
    width: 13px;
    height: 13px; } }


